.card__ {
  background-color: var(--bg-secondary);
    width: 250px;
    height: 400px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    transition: 0.3s;
    border-radius: 5px;
    overflow: hidden;
    position: relative;
    margin: 20px auto;
    display: flex;
    justify-content: center;
    transition: 0.3s;
  }

  .team-col{
    padding-top:1.5rem ;
    padding-bottom: 1rem;
  }

  .card__ img {
    width: 100%;
    height: 70%;
    object-fit: cover;
  }
 

  .card__ .card__Title {
    /* color: rgb(190, 190, 190); */
    /* padding: 10px; */
    margin: 0;
    font-size: 18px;
    text-align: center;
    text-transform: uppercase;
    /* color: #333; */
  }

  .card__ .card__Text{
    color: rgb(190, 190, 190);
    padding: 0 10px;
    font-weight: 500;
    
    font-size: 12px;
    text-align: center;
    
  }

  .card__:hover {
    box-shadow: 0 8px 16px 0 rgba(226, 195, 195, 0.2);
  }
  
  
  .icon{
    margin-top: 1rem;
    display: flex;
    flex-direction: row;
    justify-content: center;

  }
   .card_icon{
    color: rgb(190, 190, 190);
    /* color: white; */
    font-size: 20px;
    margin: 0 10px;
    
   }
    .card_icon:hover{
      color: #f8f2d5;
      font-size: 24px;
      margin: 0 10px;
      transition: 0.2s;
    }

  /* ======== MEDIA QUERIES(MEDIUM DEVICES)====== */
@media screen and (max-width:1024px) {
    .card_container{
        grid-template-columns: 1fr 1fr;
        gap:1.2rem
       
    }    
    .textcard_container{
        grid-template-columns: 1fr 1fr;
        gap:1.2rem
       
    }    
     
} 
    
    /* ======== MEDIA QUERIES(Small DEVICES)====== */
    
      @media screen and (max-width:600px) {
    .card_container{
        grid-template-columns: 1fr;
        gap:1rem;
    }

    }

